<template>
    <view class="user">
        <view class="top" @click="openTran">
            <image src="../../static/logo.png" mode=""></image>
            <view class="text">浏览历史</view>
        </view>
        <view class="content">
            <view class="row" v-for="item in listArr" :key="item.id">
                <newsBox :item="item" @click.native="goDetail"></newsBox>
            </view>
        </view>
    </view>
</template>

<script>
    export default {
        data() {
            return {
                listArr: []
            };
        },
        onLoad() {
            this.getData()
        },
        methods: {
            // 打开动画测试页面
            openTran() {
               uni.navigateTo({
                   url: '../text/text'
               }) 
            },
            // 打开新闻详情页
            goDetail() {
                uni.navigateTo({
                    url:'/pages/Detail/Detail'
                })
            },
            getData() {
                let history = uni.getStorageSync('historyArr') || []
                console.log(history)
                this.listArr = history
            },
        }
    }
</script>

